<template>
    <v-btn
    icon
    color=""
    @click="eventshowDialogCart()"
    >
        <v-badge
            color="warning"
            overlap
            :content="total_quantity"
            :value=badgeShow
        >
            <v-icon color="black">mdi-cart-outline</v-icon>
        </v-badge>
    </v-btn>
</template>

<script>
import EventBus from '../../Helpers/event-bus';
  export default {
    data: () => ({
        badgeShow:false,
        total_quantity: 0,
    }),
    created () {
    EventBus.$on("badgeshow",  () => {
        //console.log(data)
       this.getQuantity()
    })
    this.getQuantity();
  },
  methods:{
    eventshowDialogCart() {
        EventBus.$emit('dialogCart', {
            mode: true,
        })
    },

    getQuantity(){
        var cache = localStorage.getItem("cart");
        var existing = cache ? JSON.parse(cache) : {};

        if(typeof  existing === 'object' ){
            
            var result  = Object.values(existing);
            var quantity_cart = result.reduce((n, {quantity}) => n + quantity, 0);
            this.total_quantity = quantity_cart;
            //console.log( this.total_quantity)
            if(this.total_quantity > 0){
                this.badgeShow = true;
            }else{
                this.badgeShow = false
            }
        }

    }

  },
  watch: {

  },

}

</script>
