<template>
  <div class="text-center">
    <v-menu
      open-on-hover
      transition="slide-x-transition"
      v-model="menu"
      :close-on-content-click="false"
      :nudge-width="400"
      offset-y
    >
        <template v-slot:activator="{ on, attrs }">

        <v-badge
            color="warning"
            overlap
            dot
            :value=badgeShow

        >
            <v-icon
            @click="unreadNotifications()"
            v-if="badgeShow"
            dark
            v-bind="attrs"
            v-on="on"
            icon
            color="black"
            >
            mdi-bell-ring-outline
            </v-icon>
            <v-icon
            v-else
            @click="unreadNotifications()"
            dark
            v-bind="attrs"
            v-on="on"
            icon
            color="black"
            >
            mdi-bell-outline
            </v-icon>
        </v-badge>
        </template>

      <v-card>
        <v-list dense>
          <v-list-item dense>

            <v-list-item-content >
              <v-list-item-title><v-btn :disabled="fav"  @click="realAll()">{{title}} ({{messages}}) </v-btn></v-list-item-title>
            </v-list-item-content>

            <v-list-item-action>
              <v-btn
                :class="fav ? 'red--text' : ''"
                icon
                @click="fav_m()"
              >
                <v-icon>mdi-email-open-outline</v-icon>
              </v-btn>
            </v-list-item-action>
          </v-list-item>
        </v-list>

        <v-divider></v-divider>
        <div
            v-if="messages <= 0"
            class="mt-6 pb-6 ml-3 mr-3"
        >
            <v-row align="center"
                justify="center"

                >
                    <p class="text-h6 black--text">
                     All Done
                    </p>
            </v-row>
            <v-row align="center"
                justify="center">
                    <v-col
                    xs="4"
                    sm="4"
                    md="4"
                    lg="4"
                    xl="4"
                    >
                    <v-img
                    :lazy-src="$baseUrl+'storage/icons/balloon.png'"
                    max-width="270"
                    :src="$baseUrl+'storage/icons/balloon.png'"
                    ></v-img>
                    </v-col>
            </v-row>

        </div>
        <v-virtual-scroll
            v-else
            :bench="benched"
            :items="items"
            height="200"
            :item-height="fav ? '90' : '68'"
        >
            <template v-slot:default="{ item }">
            <v-list-item dense :key="item.id">
                <v-list-item-action v-if="!fav">
                <v-btn
                    icon
                    small
                    @click="read(item.id)"
                >
                <v-icon color="green">
                    mdi-check
                </v-icon>
                </v-btn>
                </v-list-item-action>

                <v-list-item-content>
                <v-list-item-title class="text-caption text--primary">
                    {{item.data.title}}
                </v-list-item-title>
                <v-list-item-title class="text-caption text--secondary text-truncate " >
                    {{item.data.body}}
                </v-list-item-title>

                <v-list-item-title class="text-caption text--disabled">
                    Create: {{formatDate(item.created_at)}}
                </v-list-item-title>
                <v-list-item-title class="text-caption text--disabled" v-if="item.read_at">
                    Read: {{formatDate(item.read_at)}}
                </v-list-item-title>
                </v-list-item-content>

                <v-list-item-action
                    v-if="item.data.url"
                >
                    <v-btn
                    icon
                    small
                    :to="item.data.url"
                    >
                        <v-icon small>
                            mdi-open-in-new
                        </v-icon>
                    </v-btn>
                </v-list-item-action>
            </v-list-item>

            <v-divider></v-divider>
            </template>
        </v-virtual-scroll>

        <v-card-actions>
        <v-btn
            dark
            block
            to="notifications"
        >
            All
        </v-btn>
        </v-card-actions>
      </v-card>
    </v-menu>
  </div>
</template>
<script>
import EventBus  from "../../Helpers/event-bus";
import moment from "moment";
  export default {
    data: () => ({
      fav: false,
      menu: false,
      badgeShow: false,
      icon:'mdi-bell-outline',
      messages: 0,
      benched: 30,
      title: 'Unread',
      items: [],
    }),
    created () {
        EventBus.$on("badgeShowNotification",  (data) => {
            //console.log(data.show)
            this.badgeShow = data.show
        });
    },
    methods:{
        formatDate(date) {
            return moment(date).format('DD-MM-YYYY HH:mm:ss');
        },
        realAll(){
            this.axios
            .get("mark-all-read-notification")
            .then(() => {
                this.unreadNotifications()
            })
        },
        read(id){
            this.axios
            .get("mark-read-notification/"+id)
            .then(() => {
                this.unreadNotifications()
            })
        },
        unreadNotifications() {
            this.axios
            .get("unread-notifications")
            .then(response => {
                this.messages = response.data.count;
                this.items = response.data.all;
                this.fav = false;
                if(this.messages > 0){this.badgeShow=true}else{this.badgeShow=false}
            })
        },
        readNotifications() {
            this.axios
            .get("read-notifications")
            .then(response => {
                this.messages = response.data.count;
                this.items = response.data.all;
                this.fav = true;
            })
        },
        fav_m(){
            this.fav = !this.fav
            switch (this.fav) {
                case true:
                    this.messages = 0;
                    this.items = [];
                    this.title = 'View Read'
                    this.readNotifications()
                    break;
                default:
                    this.messages = 0;
                    this.items = [];
                    this.title = 'View Unread'
                    this.unreadNotifications()
                    break;
            }
        }

    },
    computed: {
    },
    mounted(){
        this.unreadNotifications()
    }
  }
</script>
