<template>
  <div>
    <v-alert
      :value="show"
      :color="color"
      border="left"
      :icon="icon"
      transition="scale-transition"
      :dismissible="dismissible"
      elevation="2"
      text
      dense
    >
      <div>{{text}}</div>

      <v-divider
        class="my-4 info"
        style="opacity: 0.22"
      ></v-divider>

      <v-row
        align="center"
        no-gutters
      >
        <v-spacer></v-spacer>
        <v-col class="shrink" v-if="btText1">
          <v-btn
            color="orange"
            outlined
            class="mr-3"
            @click="action('no')"
          >
            {{btText1}}
          </v-btn>
        </v-col>

        <v-col class="shrink" v-if="btText2">
          <v-btn
            color="info"
            outlined
            @click="action('yes')"
          >
            {{btText2}}
          </v-btn>
        </v-col>
      </v-row>
    </v-alert>
  </div>
</template>
<script>
import EventBus  from "../../Helpers/event-bus";
  export default {
    data () {
      return {
        show: false,
        color:'',
        icon:'mdi-information-variant',
        text:'',
        dismissible: true,
        url:'',
        key:'',
        btText2: '',
        btText1: '',
        status:true,
      }
    },
    created () {
        EventBus.$on("showAlert",  (data) => {
            //console.log(data.mode)
            this.color = data.color
            this.icon = 'mdi-'+data.icon
            this.text = data.text
            this.show = data.show
            this.url = data.url
            this.key = data.key
            this.btText1 = data.btText1
            this.btText2 = data.btText2
        });
    },
    methods:{

        action(answer){
            this.show = false,
            this.axios
            .post("alert-update/"+this.key, { data:answer })
            .then(() => {
                this.takeAlers()
            })

            if(this.url && answer !='no'){
               // let routeData = this.$router.resolve({path: this.url});
                this.$router.push(this.url).catch(() => {});

            }

        },
        alert(data) {
        if(data.show == 0){
            this.status = false
        }else{
            this.status = true
        }
        EventBus.$emit('showAlert', {
            show: this.status,
            color: data.color,
            icon: data.icon,
            text: data.text_show,
            btText1: data.btText1,
            btText2: data.btText2,
            url: data.url_show,
            key: data.id,
        })
        },
        takeAlers(){
        this.axios
            .get('alert-take')
            .then(
                response => (
                    this.alert(response.data)
                )
            );
        },
    }
  }
</script>
