<script>

import numeral from 'numeral';
import booking from './booking.vue';
import EventBus  from "../../Helpers/event-bus";

  export default {
    components: {booking },
    props: {
        value: Boolean,
        codMeal: {
            type:Number
        },
    },
    created () {
        EventBus.$on("dialogCart",  (data) => {
            //console.log(data.mode)
            this.showDialogCart = data.mode
        });
    },
    data() {
        return {
         dialogBooking:false,
         dialogW:false,
         items: null,
         userID:null,
         cacheData:[],
         allData:null,
         showDialogCart: false,

        }
    },

    computed: {
        /*showDialogCart: {
        get () {
            return this.value
        },
        set (value) {
            this.$emit('input', value)
        }
        },*/
    },
    methods: {
        numberFormat(value){
            return numeral(value).format('0,0')
        },
        remove(meal_id){
            var cache = localStorage.getItem("cart");
            if(cache){
                var projects = JSON.parse(cache)
                this.changeDesc(projects, meal_id)
                var existing = cache;
                existing = existing ? projects : {};
                this.cacheData=existing;
                localStorage.setItem('cart', JSON.stringify(existing));
                this.getNames()
            }
        },
        changeDesc(projects, meal_id ) {
            for (var i in projects) {
                if (projects[i].meal_id == meal_id && projects[i].user == this.userID) {
                  //console.log(projects[i])
                  delete projects[i]
                  break; //Stop this loop, we found it!
                }
            }
        },

        getData(){
            var cache = localStorage.getItem("cart");
            var existing = cache ? JSON.parse(cache) : {};
            this.cacheData=existing;

            if(typeof  existing[0] === 'object' )
                this.event(true)
            else
                this.event(false)

        },
        getNames(){
            this.dialogW=true
            this.getData();
                this.axios
                .post("cart/get", { data: { cart: this.cacheData,} })
                .then(response => { this.items = response.data.data,this.allData = response.data, this.dialogW=false
                })
                .catch(() => {
                });
        },
        event(model) {
            EventBus.$emit('badgeshow', {
                model: model,
            })
        },

    },
    mounted() {
        const userData = JSON.parse(localStorage.getItem('user'));
        this.userID = userData.logged_in_user.id;

    },
    watch: {
        showDialogCart(val){
            if(val)
                this.getNames()

        },
        codMeal () {
        //console.log(val)
        }
    }

  }
</script>
