  import dialogoAvatar from '../../Auth/dialogAvatar.vue';
  import CookieLaw from 'vue-cookie-law';
  import { AbilityBuilder, Ability } from '@casl/ability';
  import messenges from "../../Helpers/Messenges.vue";
  import lang from "../../Helpers/i18n.vue";
  import badge from "../../Helpers/badge.vue";
  import alerts from "../../Helpers/alerts.vue";
  import EventBus  from "../../../Helpers/event-bus";
  import dialogViewCart from '../../Cart/dialogCart.vue';
  import notifications from '../../Auth/Notifications.vue';


  export default {
    components: { NprogressContainer,dialogo,dialogoAvatar, CookieLaw, messenges,lang, badge, dialogViewCart,alerts,notifications},

    data: () => ({
      //baseUrl:'http://localhost/cwqafkxr_eat_in_more/public/',
      //baseUrl:'https://app.eatinmore.com/',
      //baseUrl:'https://uat-app.eatinmore.com/',
      linksNames: [
        'dashboard',
        'meals',
        'requests',
        'my_cv',
      ],
      links:[
        'home',
        'meals-for-chef-cook',
        'chef-requests',
        'chef-profile',
      ],
      can:[
        'home',
        'meal_list',
        'chef_requests',
        'chef_profile',
      ],
      icons:[
        'mdi-poll',
        'mdi-noodles',
        'mdi-view-dashboard',
        'mdi-paperclip',
      ],
      badgeShow:true,
      showDialogCart:false,
      showDialogAvatar: false,
      showDialog: false,
      fav: false,
      menu: false,
      message: false,
      hints: false,
      items:[
        {
          link: 'help',
          name: 'help',
          icon: 'mdi-help',
          group: false,
          subGroup:false,
          groupList: [],
          subGroupList: [],
          can: 'home'
        },
        {
          link: 'home',
          name: 'dashboard',
          icon: 'mdi-poll',
          group: false,
          subGroup:false,
          groupList: [],
          subGroupList: [],
          can: 'home'
        },
        {
          link: 'our-chefs',
          name: 'our_chefs',
          icon: 'mdi-view-dashboard',
          group: false,
          subGroup:false,
          groupList: [],
          subGroupList: [],
          can: 'home'
        },
        {
          link: 'meals-for-chef-cook',
          name: 'meals',
          icon: 'mdi-noodles',
          group: false,
          subGroup:false,
          groupList: [],
          subGroupList: [],
          can: 'meal_list'
        },
        {
          link: '',
          name: 'chef_room',
          icon: 'mdi-chef-hat',
          group: true,
          subGroup:false,
          groupList: [
            ['requests', 'mdi-tray-alert','chef_requests','chef-requests'],
            ['achievements', 'mdi-flag-checkered','chef_achievements','chef-achievements'],
            ['my_meals', 'mdi-noodles','chef_meals','chef-meals'],
            ['new_meal', 'mdi-plus','chef_add_meal','chef-add-meal'],
            ['my_cv', 'mdi-paperclip','chef_profile','chef-profile'],
          ],
          subGroupList: [],
          can: 'chef'
        },
        {
          link: 'chefeNew',
          name: 'chef_profile',
          icon: 'mdi-account-details',
          group: false,
          subGroup:false,
          groupList: [],
          subGroupList: [],
          can: 'chef-profile'
        },
        {
          link: 'tools',
          name: 'tools',
          icon: 'mdi-hammer-screwdriver',
          group: true,
          subGroup:true,
          groupList: [],
          can: 'tools',
          subGroupList: [

                {name:'users',can:'tools_users',list: [
                                                ['users', 'mdi-account-supervisor-outline','tools_users','tools-users'],
                                            ],
                },

                {name:'roles',can:'tools_roles',list: [
                                                ['permissions', 'mdi-script-text-outline','tools_roles','tools-roles'],
                                            ],
                },

                {name:'settings',can:'tools_settings',list: [
                                                ['chef_level', 'mdi-wrench-outline','tools_level','tools-level'],
                                                ['payment_method', 'mdi-credit-card-multiple-outline','tools_payment_method','tools-payment-method'],
                                            ],
                },
                {name:'messages',can:'tools_messeges',list: [
                                                ['notify', 'mdi-bell-outline','tools_notify','tools-notify'],
                                                ['inbox', 'mdi-message-alert-outline','tools_inbox','tools-inbox'],
                                            ],
                },

          ],
        },
      ],
      states:[],
      drawer: null,
      userInfo: localStorage.getItem('user'),
      userType: '',
      userTypeName: '',
      userName:null,
      userlastName:null,
      iniName: null,
      userAvatar:null,
      modeGet: '',
      fab: false,
      status: false,
      country:'',
      currency:'',
      entity: '',
    }),
    computed:{
      
        filteredlinksNames() {
            let linksNamesList = this.linksNames
            //console.log(requestList)
            linksNamesList = linksNamesList.filter((item, index) => {
              return (this.$can(this.can[index]))
            })

            return linksNamesList;
        },

    },
  methods: {
    onScroll (e) {
    if (typeof window === 'undefined') return
    const top = window.pageYOffset ||   e.target.scrollTop || 0
    this.fab = top > 20
    },
    toTop () {
    this.$vuetify.goTo(0)
    },
    changeAvatar(){
        this.showDialogAvatar = true;
    },
    editUser(){
        this.menu = false;
        this.showDialog = true;

    },
    toggle_dark_mode: function() {
        this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
        localStorage.setItem("dark_theme", this.$vuetify.theme.dark.toString());
        this.userMode(this.$vuetify.theme.dark.toString());
    },

    userMode(dark){
      this.axios
        .get('userMode/'+dark)
        .then();
    },
    userModeGet(){
      this.axios
        .get('userModeGet')
        .then(response => (this.modeGet = response.data.mode, this.country =  response.data.country, this.currency =  response.data.alphabetic_code, this.entity =  response.data.entity));
    },
    initials(string) {
        var first=string.substring(0, 1)
      return first.toUpperCase();
    },
    logout(){
      this.$store
        .dispatch('logout')
        .then(() => {
          //this.$router.push({ name: 'home' })
        })
        .catch(err => {
            console.log(err);
        })
    },
    darkmode(){

    const theme = localStorage.getItem("dark_theme");
    //console.log(theme);
    if (theme) {
        if (theme === "true") {
            this.$vuetify.theme.dark = true;
        } else {
            this.$vuetify.theme.dark = false;
        }
    } else if (
        window.matchMedia &&
        window.matchMedia("(prefers-color-scheme: dark)").matches
    ) {
        this.$vuetify.theme.dark = true;
        localStorage.setItem(
            "dark_theme",
            this.$vuetify.theme.dark.toString()
        );
    }

    },

    logoutAll(){
      this.$store
        .dispatch('logoutAll')
        .then(() => {
          //this.$router.push({ name: 'home' })
        })
        .catch(err => {
            console.log(err);
        })
    },
    permissions(permissions){
        const { can, rules } = new AbilityBuilder(Ability);
        can(permissions, 'all');
        this.$ability.update(rules);
    },
    abilities(){
        this.axios
            .get('abilities')
            .then(response => (
            localStorage.setItem('permissions', JSON.stringify(response.data.permissions)),
            this.event(response.data.locale),
            this.permissions(response.data.permissions)
            ));
    },

    event(lang) {
        EventBus.$emit('lang', {
            lang: lang,
        })
    },

    alert(data) {
        if(data.show == 0){
            this.status = false
        }else{
            this.status = true
        }
        EventBus.$emit('showAlert', {
            show: this.status,
            color: data.color,
            icon: data.icon,
            text: data.text_show,
            btText1: data.btText1,
            btText2: data.btText2,
            url: data.url_show,
            key: data.id,
        })
    },
    takeAlers(){
      this.axios
        .get('alert-take')
        .then(
            response => (
                this.alert(response.data)
            )
        );
    },

  },

    watch: {
    $route:{
        handler(){
            this.abilities();
        },
        immediate : true
    },
    modeGet () {
    //console.log(this.modeGet)
    var dark =true
    if(this.modeGet == 1){
        dark =true
    }else{
        dark = false
    }
    localStorage.setItem("dark_theme", dark);
    this.darkmode();

    }
    },
  mounted() {
    const userData = JSON.parse(this.userInfo);
    this.abilities();
    this.userModeGet();
    this.darkmode();
    this.takeAlers()
    this.userType = userData.logged_in_user.type;
    if (this.userType == 1) {
      this.userTypeName = "Admin"
    }else if (this.userType == 2 ) {
      this.userTypeName = "Client"
    }else if(this.userType == 3){
      this.userTypeName = "Chef"
    }else{
      this.userTypeName = "No category"
    }

    var userName = userData.logged_in_user.name;
    var userlastName = userData.logged_in_user.lastName;
    this.iniName=this.initials(userName);
    this.userName = userName;
    this.userlastName =userlastName;
    this.userAvatar = userData.logged_in_user.avatar;

  },
  }
